/*
 * media queries
 * small:         30em
 * medium:        48.75em
 * medium-large:  61.25em
 * large:         75em
 * x-large:       87.5em
 */
.search__filter-heading {
  margin-bottom: 1rem;
  margin-top: 0;
  text-align: right; }
  @media (max-width: 61.1875em) {
    .search__filter-heading {
      display: none; } }

.search__filter-button {
  line-height: 0;
  margin: 0; }
  @media (min-width: 61.25em) {
    .search__filter-button {
      display: none; } }
  .search__filter-button button {
    appearance: none;
    background-color: transparent;
    border: none;
    border-radius: 0;
    margin: 0;
    padding: 0;
    background: #f5f4f2;
    display: block;
    font: 500 1.8rem/1 "Work Sans", sans-serif;
    margin: 0 -2rem;
    padding: 2rem;
    position: relative;
    text-align: left;
    text-transform: uppercase;
    width: calc(100% + 4rem); }
    .search__filter-button button::before {
      content: "";
      display: block;
      position: absolute;
      background: url(/wp-content/themes/feedthefuture/assets//images/arrow-filter.svg) no-repeat;
      height: 10px;
      margin-top: -5px;
      right: 2rem;
      top: 50%;
      transition: transform 0.2s cubic-bezier(0.75, 0, 0.125, 1);
      width: 16px; }
    .search__filter-button button[aria-expanded='true']::before {
      transform: rotate(180deg); }

.search__filter-list {
  list-style: none;
  padding: 0;
  background-color: #eeede9;
  margin: 0 -2rem; }
  @media (min-width: 61.25em) {
    .search__filter-list {
      background-color: #fff;
      margin: 0 0 4rem;
      text-align: right; } }
  .search__filter-list + .search__filter-button {
    position: relative; }
    .search__filter-list + .search__filter-button::after {
      content: "";
      display: block;
      position: absolute;
      background-color: rgba(0, 0, 0, 0.1);
      height: 1px;
      left: 0;
      right: 0;
      top: 0; }

.search__filter-list__item {
  margin: 0; }
  @media (max-width: 61.1875em) {
    .search__filter-list__item {
      padding-bottom: 1px;
      position: relative; }
      .search__filter-list__item::after {
        content: "";
        display: block;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.1);
        bottom: 0;
        height: 1px;
        left: 2rem;
        right: 2rem; }
      .search__filter-list__item:last-child::after {
        display: none; } }
  @media (min-width: 61.25em) {
    .search__filter-list__item + .search__filter-list__item {
      margin-top: 0.2rem; } }
  .search__filter-list__item label {
    border-radius: 2px;
    cursor: pointer;
    display: block;
    font-size: 1.6rem;
    line-height: 1;
    overflow: hidden;
    padding: 2rem 4rem 2rem 2rem;
    position: relative;
    transition: all 0.2s cubic-bezier(0.75, 0, 0.125, 1); }
    @media (min-width: 61.25em) {
      .search__filter-list__item label {
        font-size: 1.4rem;
        margin-right: -2rem;
        padding: 1rem 2rem 1rem 4rem;
        width: calc(100% + 2rem); }
        .search__filter-list__item label:focus, .search__filter-list__item label:hover {
          background-color: rgba(35, 124, 154, 0.2); } }
    .search__filter-list__item label.-selected {
      background-color: rgba(35, 124, 154, 0.1);
      cursor: default; }
      .search__filter-list__item label.-selected:focus, .search__filter-list__item label.-selected:hover {
        background-color: #237c9a;
        color: #fff; }
        .search__filter-list__item label.-selected:focus .search__filter-list__ex, .search__filter-list__item label.-selected:hover .search__filter-list__ex {
          transform: rotate(90deg); }
          .search__filter-list__item label.-selected:focus .search__filter-list__ex::before, .search__filter-list__item label.-selected:focus .search__filter-list__ex::after, .search__filter-list__item label.-selected:hover .search__filter-list__ex::before, .search__filter-list__item label.-selected:hover .search__filter-list__ex::after {
            background-color: #fff; }
  .search__filter-list__item input[type='checkbox'] {
    left: -50px;
    margin: 0 0 0 1rem;
    position: absolute; }
    [data-whatintent='keyboard'] .search__filter-list__item input[type='checkbox'] {
      position: static; }

.search__filter-list__ex {
  cursor: pointer;
  display: block;
  height: 2rem;
  margin-top: -1rem;
  position: absolute;
  right: 2rem;
  top: 50%;
  transition: transform 0.3s cubic-bezier(0.75, 0, 0.125, 1);
  width: 2rem; }
  @media (min-width: 61.25em) {
    .search__filter-list__ex {
      left: 1rem; } }
  .search__filter-list__ex::before, .search__filter-list__ex::after {
    content: "";
    display: block;
    position: absolute;
    background-color: #014054;
    height: 2px;
    left: 50%;
    margin: -1px 0 0 -7px;
    top: 50%;
    transition: all 0.2s cubic-bezier(0.75, 0, 0.125, 1);
    width: 14px; }
  .search__filter-list__ex::before {
    transform: rotate(45deg); }
  .search__filter-list__ex::after {
    transform: rotate(-45deg); }
