.search__filter-heading {
  margin-bottom: unit(0.5);
  margin-top: 0;
  text-align: right;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    display: none;
  }
}

.search__filter-button {
  line-height: 0;
  margin: 0;

  @include respond-to('medium-large') {
    display: none;
  }

  button {
    @include reset-button;
    background: $color--cararra;
    display: block;
    font: $font-weight--bold 1.8rem/1 $font-family--sans;
    margin: 0 unit(-1);
    padding: unit(1);
    position: relative;
    text-align: left;
    text-transform: uppercase;
    width: calc(100% + #{unit(2)});

    &::before {
      @include pseudo;
      background: url(#{$path}/images/arrow-filter.svg) no-repeat;
      height: 10px;
      margin-top: -5px;
      right: unit(1);
      top: 50%;
      transition: transform 0.2s $ease--snappy;
      width: 16px;
    }

    &[aria-expanded='true']::before {
      transform: rotate(180deg);
    }
  }
}

.search__filter-list {
  @include reset-list;
  background-color: darken($color--cararra, 3);
  margin: 0 unit(-1);

  @include respond-to('medium-large') {
    background-color: $color--white;
    margin: 0 0 unit(2);
    text-align: right;
  }

  + .search__filter-button {
    position: relative;

    &::after {
      @include pseudo;
      background-color: rgba($color--black, 0.1);
      height: 1px;
      left: 0;
      right: 0;
      top: 0;
    }
  }
}

.search__filter-list__item {
  margin: 0;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    padding-bottom: 1px;
    position: relative;

    &::after {
      @include pseudo;
      background-color: rgba($color--black, 0.1);
      bottom: 0;
      height: 1px;
      left: unit(1);
      right: unit(1);
    }

    &:last-child::after {
      display: none;
    }
  }

  @include respond-to('medium-large') {
    + .search__filter-list__item {
      margin-top: unit(0.1);
    }
  }

  label {
    border-radius: 2px;
    cursor: pointer;
    display: block;
    font-size: 1.6rem;
    line-height: 1;
    overflow: hidden;
    padding: unit(1) unit(2) unit(1) unit(1);
    position: relative;
    transition: all 0.2s $ease--snappy;

    @include respond-to('medium-large') {
      font-size: 1.4rem;
      margin-right: unit(-1);
      padding: unit(0.5) unit(1) unit(0.5) unit(2);
      width: calc(100% + #{unit(1)});

      @include hover {
        background-color: rgba($color--jelly-bean, 0.2);
      }
    }

    &.-selected {
      background-color: rgba($color--jelly-bean, 0.1);
      cursor: default;

      @include hover {
        background-color: $color--jelly-bean;
        color: $color--white;

        .search__filter-list__ex {
          transform: rotate(90deg);

          &::before,
          &::after {
            background-color: $color--white;
          }
        }
      }
    }
  }

  input[type='checkbox'] {
    left: -50px;
    margin: 0 0 0 unit(0.5);
    position: absolute;

    [data-whatintent='keyboard'] & {
      position: static;
    }
  }
}

.search__filter-list__ex {
  cursor: pointer;
  display: block;
  height: unit(1);
  margin-top: unit(-0.5);
  position: absolute;
  right: unit(1);
  top: 50%;
  transition: transform 0.3s $ease--snappy;
  width: unit(1);

  @include respond-to('medium-large') {
    left: unit(0.5);
  }

  &::before,
  &::after {
    @include pseudo;
    background-color: $color--sherpa-blue;
    height: 2px;
    left: 50%;
    margin: -1px 0 0 -7px;
    top: 50%;
    transition: all 0.2s $ease--snappy;
    width: 14px;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
